    width: 100%;
    .vh(80);
    .font-size(28);
    color: #ffffff;
  }
  .weather-box {
    width: 100%;
    background: #0389c7;
    border-radius: 8px;
    box-sizing: border-box;
    .padding(12, 12, 12, 12);
    .water-iteam {
      display: flex;
      justify-content: space-around;
      color: #ffffff;
      .font-size(28);
      align-items: center;
    }
    .mony-font {
      .font-size(18);
      text-align: left;
      .line-height(28);
    }
    .water-iteam1 {
      .font-size(36);
      justify-content: flex-start;
      .padding(0, 0, 0, 36);
      .yx {
        .margin(0, 0, 0, 26);
      }
    }
  }
  .gd-box {
    flex: 1;
    width: 100%;
