    flex-direction: column;
    .margin(16, 0, 0, 0);
    .tit {
      width: 100%;
      .vh(40);
      .font-size(28);
      color: #ffffff;
    }
    .swiper-box {
      flex: 1;
    }
    .swiper-container {
      height: 100%;
      width: 100%;
      .swiper-wrapper {
        width: 100%;
        height: 100%;
        color: #ffffff;
        .swiper-slide{
          display: flex;
          flex-direction: column;
        }
      }
      .itampaiming{
        display: flex;
        flex: 1;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        background: #232434;
        box-sizing: border-box;
        .padding(0,12,0,0);
        border-radius: 4px;
        .font-size(24);
        .margin(8,0,0,0);
        .indexnum{
          .vw(120);
          color: rgb(32, 192, 254);
          text-align: left;
          box-sizing: border-box;
          .padding(0,0,0,10);
        }
        .indexname{
          display: flex;
          flex: 1;
          .font-size(22);
          justify-content: space-between;
        }
      }
    }
  }
}
</style>
